<template>
	<div class="login-form">
		<h3></h3>
		<Form :model="formItem" :label-width="80">
			<Form-item label="">
				<Input v-model="formItem.loginName" placeholder=""></Input>
			</Form-item>
			<Form-item label="">
				<Input v-model="formItem.passWord" placeholder=""></Input>
			</Form-item>
			<Form-item label="">
				<Checkbox-group v-model="formItem.checkbox">
					<Checkbox label=""></Checkbox>
				</Checkbox-group>
			</Form-item>
			<Form-item>
				<Button type="primary" v-on:click="login"></Button>
				<Button type="ghost" v-on:click="registr" style="margin-left: 8px"></Button>
			</Form-item>
		</Form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				formItem: {
					loginName: '',
					passWord: '',
					checkbox: []
				}
			}
		},
		methods: {
			login: function() {
				this.$Message.info('');
				this.$router.push('BackEnd')
			},
			registr: function() {
				this.$Notice.open({
					title: '',
					desc: ''
				});
			}
		}
	}
</script>

<style>
	.login-form {
		width: 480px;
		margin: 0 auto;
		background: #FFFFFF;
		box-shadow: 0 0 10px #DDDDDD;
		border-radius: 10px;
		padding: 20px;
		position: relative;
		top: 40px;
	}
	
	.login-form h3 {
		text-align: center;
		padding-bottom: 20px;
	}
</style>


